<template>
  <div>
    <HomeButton />
    <div class="modal-backdrop"></div>
    <div class="modal">
      <div class="modal-content">
        <div class="status">
          <span v-if="inviteSent">Waiting for opponent's decision</span>
          <span v-else-if="opponentDecline">The opponent has declined your invite</span>
          <span v-else>You can find an opponent or play with random person</span>
        </div>
        <div v-if="users.length > 1">
          <button class="btn" @click="randomOpponent">Random</button>
          <ul>
            <div class="search">
              <label>Search</label>
              <input class="search__input" type="text" v-model="searchName" @input="sortUsers" placeholder="Opponent's name">
            </div>
            <li v-for="(user, id) in (searchName ? foundUsers : users)" v-if="user !== nickname" :key="id">
              {{ user }}
              <button class="invite" :class="(id_pressed === id && inviteSent) ? 'btn--red' : 'btn--blue'" @click="invite(user, id)" :disabled="(id_pressed !== id && inviteSent)">{{ (id_pressed === id && inviteSent) ? 'cancel' : 'duel' }}</button>
            </li>
          </ul>
        </div>
        <p v-else>There are no online users now. You can try another mode.</p>
      </div>
    </div>
  </div>
</template>
<script>
import HomeButton from '../components/HomeButton'

export default {
  data () {
    return {
      searchName: '',
      id_pressed: null,
      foundUsers: []
    }
  },
  props: {
    users: {
      type: Array
    },
    nickname: {
      type: String
    },
    randomOpponent: {
      type: Function
    },
    sendInvite: {
      type: Function
    },
    inviteSent: {
      type: Boolean
    },
    opponentDecline: {
      type: Boolean
    }
  },
  methods: {
    sortUsers () {
      this.foundUsers.length = 0
      for (let i = 0; i < this.users.length; i++) {
        if (this.users[i].startsWith(this.searchName)) {
          this.foundUsers.push(this.users[i])
        }
      }
    },
    invite (user, id) {
      this.id_pressed = id
      this.sendInvite(user)
    }
  },
  components: {
    HomeButton
  }
}
</script>

<style scoped>
.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal {
  position: fixed;
  width: 100%;
  max-width: 500px;
  max-height: 100vh;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  padding-bottom: 3px;
  z-index: 1051;
  background: #fff;
  overflow-y: auto;
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.3);
}
.modal::before {
  background: linear-gradient(to right, #9BFFE6 0%, #A3CCFF 100%);
  content: "";
  display: block;
  height: 3px;
  width: 100%;
}
@media screen and (max-width: 500px) {
  .modal {
    height: 100vh;
  }
}
.modal-content {
  height: auto;
  position: relative;  
  z-index: 1052;
  background: #fff;
  font-size: 24px;
}
.status {
  height: 100px;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #e5e5e5;
}
label {
  margin-right: 10px;
}
ul {
  list-style: none;
  padding: 0;
  max-height: 300px;
  overflow-y: auto;
}
ul li {
  position: relative;
  margin-bottom: 10px;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  transition: all 0.3s;
  background-color:#f2f2f2;
  border-left:5px solid #003842;
}
ul li:hover {
  background-color:#f9f9f9;
  border-color:#00BDE8;
}
.invite {
  width: 100px;
  font-family: inherit;
  font-size: inherit;
  cursor: pointer;
  outline: none;
  background-color: #fff;
  border-radius: 5px;
}
</style>


