<template>
  <div>
    <Loader v-show="!loaded" />
    <div class="learn_notification">
      Choose Continent
    </div>
    <div id="map" v-if="loaded">    
      <Map :geojson="geojson" :onEachFeature="onEachFeature" />
    </div>
  </div>
</template>
<script>
import router from '../router'
import Loader from './Loader'
import Map from './Map'

export default {
  data () {
    return {
      geojson: [],
      loaded: false
    }
  },
  created () {
    this.createGeo()
  },
  methods: {
    createGeo () {
      this.$http.get(`../json/continents.json`)
        .then(response => {
          this.geojson = [...response.body.features]
          this.loaded = true
        }, response => {
          console.log(response)
      })
    },
    onEachFeature (feature, layer) {
      layer.setStyle({fillColor: this.randomColor()})
      layer.on('click', () => {
        router.push({ 
          name: 'Subject',
          params: { 
            name: layer.feature.properties.continent.replace(/ /g, '').toLowerCase(), 
          }
        })
      })
    },
    randomColor () {
      return `rgb(${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)})`
    }
  },
  components: {
    Loader,
    Map
  },
}
</script>
<style scoped>
.learn_notification {
  position: absolute;
  right: 50px;
  top: 50px;
  font-size: 32px;
  color: #fff;
  z-index: 900;
}
@media screen and (max-width: 400px) {
  .learn_notification {
    right: 0;
    top: 10px;
    width: 100%;
    font-size: 20px;
  }
}
</style>


